.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

/*
 *  CSS for the NavBar component
 */

.clearfix::after {
    content: "";
    clear: both;
    display: table;
}

.clicky-nav-bar {
    background: darkslateblue;
    color: white;
    width: 100%;
    height: auto;
    padding: 10px 0px;
    position: fixed;
    top: 0;
}

.clicky-name {
    font-size: 3vw;
    font-weight: 900;
}

.clicky-nav-bar-name,
.clicky-nav-bar-msg,
.clicky-nav-bar-results {
    display: inline-block;
    text-align: center;
    width: 33%;
}

.clicky-msg,
.clicky-results {
    font-size: 2vw;
}

a {
    color: white;
    text-decoration: none;
}
/*
 *  CSS for the Header component
 */

.clicky-header {
    background-color: darkslategray;
    color: white;
    font-size: 2vw;
    text-align: center;
    padding: 10px 0;
    margin: 5rem 0 0 0;
}

